.dgu-block__wrapper:before {
  content: "";
  background: linear-gradient(90deg, var(--bs-blue) 0%, rgba(6, 27, 49, 0) 150%);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
}
@media (min-width: 768px) {
  .dgu-block__wrapper img.dgu {
    transform: scale(1.5);
    margin-left: 11%;
    object-position: 0% 66%;
  }
}
.dgu-block .main-slider__title {
  text-transform: inherit;
}

.bg-extra-light {
  background: var(--extra-light-background) !important;
}

.dgu-info__wrapper {
  margin-top: -4.5rem;
}
@media (max-width: 768px) {
  .dgu-info__wrapper {
    margin-top: -1rem;
  }
}
.dgu-info__wrapper {
  grid-template-columns: 1fr 1fr 1fr;
}
@media (max-width: 500px) {
  .dgu-info p {
    font-size: 0.875rem;
  }
}

.large-p {
  padding-bottom: 8.5rem;
}
@media (max-width: 768px) {
  .large-p {
    padding-bottom: 4.5rem;
  }
}

.dgu__description {
  font-size: 1.125rem;
}
.dgu__background {
  background: var(--light-background);
}
@media (max-width: 768px) {
  .dgu__tarifs {
    font-size: 0.875rem;
  }
}
.dgu__height {
  max-height: 380px;
  min-height: 182px;
}
@media (min-width: 992px) {
  .dgu.slider-container {
    overflow: hidden;
  }
}

.big-p {
  font-size: 1.125rem;
}
@media (max-width: 768px) {
  .big-p {
    font-size: 1rem;
  }
}

.dgu__cards {
  grid-template-columns: 1fr 1fr 1fr 1fr;
}
@media (max-width: 992px) {
  .dgu__cards {
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 768px) {
  .dgu__cards {
    grid-template-columns: 1fr;
  }
}
.dgu__cards .d-flex:not(.files-item__svg) {
  background: var(--extra-light-background);
  height: 232px;
}
@media (max-width: 768px) {
  .dgu__cards .d-flex:not(.files-item__svg) {
    height: fit-content;
  }
}
.dgu__cards .files-item__svg {
  min-width: 48px;
}
@media (max-width: 768px) {
  .dgu__cards .files-item__svg {
    min-width: 40px;
    font-size: 0.875rem;
    min-height: 40px;
  }
}
@media (max-width: 768px) {
  .dgu__cards p {
    font-size: 0.875rem;
  }
}

.warning-block {
  color: #450A0A;
  background: #FEF2F2;
  border-left: 2px solid #EF4444;
}
@media (max-width: 768px) {
  .warning-block {
    font-size: 0.875rem;
  }
}

.other_energy {
  width: 16px;
  height: 20px;
}
@media (max-width: 768px) {
  .other_energy {
    width: 13.5px;
    height: 16.5px;
  }
}

.h-fit {
  height: fit-content;
}